    color: #FFFFFF;
    background: linear-gradient(94.39deg, #EF7F1A -1.37%, #EF5A1A 100%);
    transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
    padding-left: 2em;
    padding-right: 2em;
    box-shadow: 0 0 100px 20px rgba(239,90,26, 0.5);
    &:hover{
        background: linear-gradient(94.39deg, #EF7F1A -1.37%, #EF5A1A 100%);
