.main {
}
.topbar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;

    width: 100%;
    // background: #000;
    background: #0E0D16;
    height: 95px;
    padding-left: 55px;
    padding-right: 65px;
    box-sizing: border-box;

    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    
}
.topbar-logo {
    
}
.topbar-menu {
    width: 100%;
    padding-left: 80px;
    padding-right: 80px;
    box-sizing: border-box;
    ul{
        display: flex;
        align-items: center;
        // justify-content: space-between;
        justify-content: center;
        flex-wrap: nowrap;
        list-style: none;
        li{
            a{
                // white-space: nowrap;
                display: block;
                font-size: 20px;
                line-height: 1.15;
                text-align: center;
                text-decoration: none;
                color: #FFFFFF;
                transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
                padding: 1em 1em;
                position: relative;
                &:hover{
                    color: #EF7F1A;
                }
                &[data-active="1"]{
                    color: #EF7F1A;
                    &::before{
                        content: ' ';
                        display: block;
                        position: absolute;
                        left: 0;
                        margin-left: 1em;
                        bottom: 0;
                        width: calc(100% - 2em);
                        height: 2px;
                        background: #EF7F1A;
                    }
                    &:hover{
                        cursor: default;
                    }
                }
                    
            }
            // &:first-of-type a{
            //     padding-left: 0;
            // }
            // &:last-of-type a{
            //     padding-right: 0;
            // }
        }
    }
}
.topbar-tel {
    a{
        white-space: nowrap;
        display: block;
        font-weight: bold;
        font-size: 20px;
        line-height: 1.2;
        color: #EF7F1A;
        text-decoration: none;
        background: url(../images/ico-tel.svg) no-repeat left center;
        padding-left: 32px;
        transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
        &:hover{
            opacity: 0.7;
            text-decoration: none;
        }
    }
}
.index-first-screen {
    position: relative;
    margin-bottom: 117px;
        &__image{
            background: url(../images/index-first-screen.jpg) no-repeat center center;
            height: calc(100vh - 95px);
            // img{
            //     display: block;
            //     width: 100%;
            // }
        }

		&__description {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 5;

            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: nowrap;
            font-weight: 600;
            font-size: 150px;
            line-height: 1.19;
            text-align: center;

            color: #FFFFFF;

            opacity: 0.6;
            text-shadow: 0px -20px 150px #22FFBD, 0px 20px 150px #FF00C7;
		}



		&__to-down {
            position: absolute;
            // bottom: 40px;
            // bottom: 150px;
            // left: 50%;

            top: calc(100vh - 140px);
            right: 20px;

            z-index: 25;
            a{
                display: block;
                width: 150px;
                // padding-top: 54px;
                padding-top: 75px;
                background: url(../images/arrow-down.svg) no-repeat center top;
                margin-left: -75px;
                text-align: center;
                transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

                font-size: 24px;
                line-height: 1.166666666666667;
                text-align: center;
                text-decoration: none;
                color: rgba(255, 255, 255, 0.58);

                animation: index-first-screen-arrow-to-down-bounce 1s ease-in-out infinite alternate;
                &:hover{
                    opacity: 0.7;
                }
                
            }
		}
}


@keyframes index-first-screen-arrow-to-down-bounce{
    from{
        background-position: center 0;
    }

    to{
        background-position: center 30px;
    }
}

.link-to-up {
    position: fixed;
    bottom: 78px;
    right: 70px;
    z-index: 50;
    a{
        display: block;
        background: url(../images/arrow-up.svg) no-repeat center center;
        width: 56px;
        height: 56px;
        transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
        &:hover{
            opacity: 0.7;
        }
    }

    @media only screen and (max-width : 1240px) {
        right: 20px;
    }
}







