.block-footer-social {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: nowrap;
		&__item {
            width: 35px;
            height: 35px;
            margin-right: 12px;
            position: relative;

            &::before{
                content: ' ';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 100%;
                opacity: 1;
                transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
            }
            &::after{
                content: ' ';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
            }
            &:hover::before{
                opacity: 0;
            }
            &:hover::after{
                opacity: 1;
            }


			&_vk {
                &::before{
                    background: url(../images/ico-soc_vk.svg) no-repeat center center;
                }
                &::after{
                    background: url(../images/ico-soc_vk-orange.svg) no-repeat center center;
                }
			}

			&_inst {
                &::before{
                    background: url(../images/ico-soc_inst.svg) no-repeat center center;
                }
                &::after{
                    background: url(../images/ico-soc_inst-orange.svg) no-repeat center center;
                }
			}

			&_fb {
                &::before{
                    background: url(../images/ico-soc_fb.svg) no-repeat center center;
                }
                &::after{
                    background: url(../images/ico-soc_fb-orange.svg) no-repeat center center;
                }
			}
		}
}
